/* Home Page */
#Home {
  background-color: black;
  background-image: linear-gradient(180deg, rgba(0, 0, 0) 0%, rgba(7, 4, 47, 0.5) 100%);
}

#Content {
  justify-self: center;
  min-height: 100vh;
  max-width: 1500px;
}

@media (max-width: 1600px) {
  #Content {
      width: 80%;
  }
}

@media (max-width: 800px) {
  #Content {
      width: 90%;
  }
}

/* Not Found Page */
#not-found {
  justify-self: center;
  margin-top: 20%;
}

/* Map Page */
.county-card {
  background-color: white;
  color: black;
  position: absolute;
  border: 2.8px solid black;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 225px;
}

#county-name {
  word-wrap: break-word;
  overflow-wrap: break-word;
  text-align: center;
  margin: 10px 0;
  font-weight: bold;
}

#card-options {
  width: 100%;
}

#card-options p {
  margin: 0;
  padding: 10px 0;
  box-sizing: border-box;
  text-align: center;
  display: block;
  cursor: pointer;
}

#lived {background-color: #FF7E7E;}
#stayed {background-color: #FFB57E;}
#visited {background-color: #FFE57E;}
#stopped {background-color: #A8FFBE;}
#passed {background-color: #88AEFF;}

.radius {
  border-radius: 7.5px;
}

.buttons {
  position: absolute;
  top: 10px;
  left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 2.8px solid black;
}

.buttons svg {
  margin: 5px 5px;
  height: 25px;
  width: 25px;
  cursor: pointer;
  fill: black;
}

.tooltip {
  background-color: black;
  color: #fff;
  padding: 5px;
  font-size: 12px;
  text-align: center;
  z-index: 1000;
}

.map-tooltip {
  position: absolute;
  pointer-events: none;
}

.button-tooltip {
  all: unset;
  position: relative;
  display: inline-block;
}

/* Focus ring for tabbing */
.button-tooltip:focus-visible {
  outline: 2px solid black;
  border-radius: 5px;
}

.button-tooltip-text {
  visibility: hidden;
  position: absolute;
  top: 100%; /* Place below */
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  opacity: 0;
  transition: opacity 0.2s;
}

.button-tooltip:hover .button-tooltip-text {
  visibility: visible;
  opacity: 1;
}

@media (max-width: 768px) { /* Target mobile devices */
  .map-tooltip, .tooltip-text {
    display: none;
  }
}